.btn-svg,
.btn-svg--faq {

  background-color: transparent;
  border: none;
  cursor: pointer;

  svg {
    width: size(40px);
    height: size(40px);

    @include vp-767 {
      width: size(30px);
      height: size(30px);
    }

    path {
      stroke: #102654;
    }

    rect {
      stroke: #102654;
    }
  }

  @include hover-focus {
    circle {
      stroke: #102654;
    }
  }

  @include active {
    svg {
      fill: #102654
    }

    circle {
      stroke: #102654;
    }

    path {
      stroke: white;
    }
  }

  &:disabled {
    cursor: not-allowed;
    pointer-events: none;

    path {
      opacity: 0.3;
    }
  }
}
